import boxed from "assets/img/logos/not-brex/grayscale/light-text/boxed.png";
import gravity from "assets/img/logos/not-brex/grayscale/light-text/gravity.png";
import touchOfModern from "assets/img/logos/not-brex/grayscale/light-text/touch-of-modern.png";
import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";
import CompanyTypes from "utils/companyTypes";

import UndecoratedOutboundLink from "components/reusable/atoms/links/UndecoratedOutboundLink";

